@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@400;500&family=Montserrat:wght@700&family=Roboto:wght@400;700&display=swap);
.header{height:10vh;background-color:#f0f0f0;display:flex;align-items:center;justify-content:space-between;padding:0 15px}.logo{display:block;color:#000;text-decoration:none;font-size:32px;line-height:1.2;text-transform:uppercase;font-family:"Fira Sans";letter-spacing:1px}.nav__link{font-size:18px;line-height:1.2;text-transform:uppercase;text-decoration:none;font-family:"Fira Sans", sans-serif;color:#000;margin-left:30px}

.page__inner{display:grid;grid-template-columns:1fr 1fr;grid-gap:30px;margin:30px 15px;padding:30px;background-color:#fff;border-radius:10px}

.form__header{display:flex;align-items:center;height:40px;margin-bottom:22px}.form__header-title{font-size:20px;line-height:36px;font-weight:700;font-family:Roboto, sans-serif}.form__header .btn-edit{margin-left:14px}.form__body{display:grid;grid-template-columns:1fr 1fr;grid-gap:10px}.form__item{font-family:"Fira Sans", sans-serif;line-height:18px;display:flex;flex-direction:column;margin-bottom:35px}.form__item-required{color:#ff5500}.form__item-label{font-size:14px;margin-bottom:14px;position:relative}.form__item-value{font-size:16px;font-weight:500;text-transform:capitalize}.form__item-input{padding:13px;border-radius:4px;border:1px solid #e8e8e8;text-transform:capitalize}.form__btn{font-family:Montserrat, sans-serif;width:160px;height:50px;text-transform:uppercase;background-color:#6bd098;border:none}

.dashboard{width:100%;height:90vh;display:flex;align-items:center;justify-content:center}

.button{display:flex;justify-content:center;align-items:center;position:relative;min-width:160px;padding:10px 16px;height:50px;text-transform:uppercase;font-family:"Montserrat", sans-serif;border-radius:4px;border:2px solid #000;outline:none;font-weight:700;font-size:16px;line-height:18px;grid-gap:10px;gap:10px}.button-reversed{flex-direction:row-reverse}.button-icon{width:20px;height:20px;background-color:#d22323;display:flex;align-items:center;justify-content:center}.button-success{color:#fff;background-color:#6bd098;border:none}.button-success:hover,.button-success:active{background-color:#6bc083}.button-success:disabled{background-color:#dedede}.button-success:disabled>div{opacity:0.5}.button-success-outline{background-color:#fff;color:#6bd098;border:2px solid #6bd098}.button-success-outline:hover,.button-success-outline:active{color:#6bc083;border-color:#6bc083}.button-secondary{color:#fff;background-color:#007bff;border:none}.button-secondary:hover,.button-secondary:active{background-color:#51bcda}.button-secondary:disabled{background-color:#dedede}.button-secondary:disabled>div{opacity:0.5}.button-secondary-outline{background-color:#fff;color:#007bff;border:2px solid #007bff}.button-secondary-outline:hover,.button-secondary-outline:active{color:#51bcda;border-color:#51bcda}.button-error{color:#fff;background-color:#ef8157;border:none}.button-error:hover,.button-error:active{background-color:#dc6c41}.button-error:disabled{background-color:#dedede}.button-error:disabled>div{opacity:0.5}.button-error-outline{background-color:#fff;color:#ef8157;border:2px solid #ef8157}.button-error-outline:hover,.button-error-outline:active{color:#dc6c41;border-color:#dc6c41}

.buttons{display:flex;width:100%;height:90vh;display:flex;align-items:center;justify-content:center}

html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}body{background-color:#e7e6e6}

